@import url(https://fonts.googleapis.com/css?family=Comfortaa:300|Montserrat+Alternates:900i|Nunito:200&subset=cyrillic);
body {
  margin: 0;
  padding: 0;
  padding: 0 2rem;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

/*********************************************************  CSS for "Users.js"*/
.users__wrapper {
  align-self: center;
  height: 40%;
  margin: auto;
  padding: 2rem;
  font-size: 0.8em;
}

.users__header {
  padding-bottom: 1rem;
  border-bottom: 1px solid #274684;
}
.users__list {
  list-style: none;
  height: 17vh;
  width: 100%;
  margin: 0.5rem 0;
  overflow-x: hidden;
}

/***************************************************************** Every user */
.users__user {
  width: 100%;
  padding: 0.5rem;
  margin: 0.3rem 0;
  font-size: inherit;
  cursor: pointer;
}
.users__user:hover {
  color: #011c35;
  background-color: #eee;
  font-weight: 600;
}

/************************************************************************ btn */
.users__btn {
  width: 100%;
  padding: 0.5rem;
  font-size: inherit;
  border: 1px solid #eee;
  cursor: pointer;
}
.users__btn:hover {
  color: #011c35;
  background-color: #eee;
}

/********************************************************************** MEDIA */
@media (max-width: 780px) {
  .users__wrapper {
    font-size: 0.8em;
  }
}
@media (max-width: 400px) {
  .users__wrapper {
    font-size: 0.6em;
  }
}

/*******************************************************  CSS for "NewUser.js"*/

.newUser__wrapper {
  width: 100%;
  height: 100vh;
  padding: 0 30%;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

/**************************************************************** input field */
.field__wrapper {
  display: flex;
  justify-content: space-between;
  align-items: baseline;
  padding: 0.5rem;
}
.field {
  border: 1px solid #eee;
  padding: 0.5rem;
  margin: 0.1rem;
  font-size: inherit;
}
.field:focus {
  outline: none;
  background-color: #eee;
  color: #274684;
}

/****************************************************************** radio-btn */
.radio {
  position: absolute;
  opacity: 0;
  cursor: pointer;
}
.checkmark {
  top: 0;
  left: 0;
  height: 20px;
  width: 20px;
  background-color: #eee;
  border-radius: 50%;
}
.field:checked ~ .checkmark {
  background-color: #2196F3;
  border: 3px solid #eee;
}

/************************************************************************ btn */
.newUser__btn {
  width: 100%;
  padding: 0.5rem;
  margin: 1rem 0;
  font-size: inherit;
  border: 1px solid #eee;
  cursor: pointer;
}
.newUser__btn:hover {
  color: #011c35;
  background-color: #eee;
}

/********************************************************************** MEDIA */
@media (max-width: 1000px) {
  .newUser__wrapper {
    font-size: 0.8em;
  }
}
@media (max-width: 700px) {
  .newUser__wrapper {
    font-size: 0.7em;
    padding: 0 20%;
  }
}
@media (max-width: 500px) {
  .newUser__wrapper {
    padding: 0 10%;
    font-size: 0.6em;
  }
}

/*********************************************  CSS for -component- "Title.js"*/

.title__wrapper {
  width: 100%;
  height: 50vh;
  align-items: center;
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: 1fr 1fr;
  grid-template-areas:
   "logo text"
   "logo text";
}

/***************************************************************** Title logo */
.title__logo {
  grid-area: logo;
  width: 35%;
  margin-left: 40%;
  padding-top: 10%;
}

/***************************************************************** Title text */
.title-text__wrapper {
  padding-right: 30%;
  padding-top: 10%;
  grid-area: text;
}
.title-text__wrapper>hr{
  border: none;
  border-top: 1px solid #274684;
}
.title-text__header {
  padding: 3% 5%;
  font-size: 2.5em;
  text-align: center;
  font-family: 'Nunito', sans-serif;
  font-weight: 200;
}
.title-text__slogan {
  font-size: 1em;
  letter-spacing: 1px;
  margin: 0.1rem;
  padding: 3% 5%;
}

/********************************************************************** MEDIA */
@media (max-width: 780px) {
  .title__wrapper {
    height: auto;
    grid-template-areas:
      "logo logo"
      "text text";
   }
   .title__logo {
     width: 30%;
     margin: auto;
   }
   .title-text__wrapper {
     padding: 0;
     grid-area: text;
     border-bottom: 1px solid #274684;
   }
   .title-text__header {
     font-size: 1.7em;
   }
   .title-text__slogan  {
     font-size: 0.8em;
   }
}
@media (max-width: 400px) {
  .title-text__header {
    font-size: 1.2em;
  }
  .title-text__slogan  {
    font-size: 0.6em;
  }
}

/********************************************************  CSS for "Header.js"*/

.header__wrapper {
  width: 100%;
  padding: 0 80% 0 10%;
  border-bottom: 1px solid #eee;
  display: flex;
  justify-content: center;
  align-items: center;
  grid-area: header;
}

/*********************************************************************** Logo */
.header__logo {
  width: 3.5rem;
  cursor: pointer;
  padding: 0.3rem;
}

/*********************************************************************** Text */
.header__text {
  font-family: 'Nunito', sans-serif;
  font-weight: 200;
  font-size: 1.3em;
  letter-spacing: 3px;
  padding-left: 1rem;
  color: #eee;
  cursor: pointer;
}

/********************************************************************** MEDIA */
@media (max-width: 500px) {
  .header__logo {
    width: 2.5rem;
    margin-left: 1rem;
  }
  .header__text {
    font-size: 0.8em;
  }
}

/*****************************************************  CSS for "TitlePage.js"*/

.titlePage__wrapper {
  height: 100vh;
  width: 100vw;
  text-align: center;
  display: flex;
  flex-direction: column;
}

.icon {
  font-size: 1.2em;
  margin-left: 1rem;
  color: inherit;
  background-color: inherit;
}

/*********************************************************  CSS for "Tasks.js"*/

.tasks__wrapper {
  overflow-x: hidden;
  width: 100%;
  padding-right: 20%;
  margin: auto;
  grid-area: tasks;
}

/********************************************************************* Header */
.tasks__header {
  margin: 1rem;
  text-align: center;
  font-size: 2em;
  font-family: 'Montserrat Alternates', sans-serif;
  color: rgba(218, 218, 218, 0.2);
}

/************************************************************* For every task */
.task__wrapper {
  margin: 0.7rem 0;
  display: flex;
  justify-content: flex-start;
  cursor: pointer;
}

/****************************************************************** Task text */
.task__text {
  width: 60%;
  padding: 0.5rem 1rem;
  font-size: 1.1em;
}
.complete{
  text-decoration: line-through;
  color: #dadada;
  background-color: rgba(218, 218, 218, 0.2);
}

/**************************************************************** Task number */
.task__number {
  font-family: 'Montserrat Alternates', sans-serif;
  width: 10%;
  text-align: center;
  align-self: center;
  font-weight: 900;
  font-size: 4.8em;
  margin: -2rem 0;
  background-color: rgba(0, 123, 255, 0);
  color: rgba(218, 218, 218, 0.2);
}

/*************************************************** New task or editing task */
.newTask, .editTask {
  width: 69%;
  margin: 0 0 0 1%;
  border: 1px solid #eee;
}
.newTask:focus, .editTask:focus {
  background-color: #eee;
  color: #011c35;
}

/******************************************************************** Buttons */
.btn {
  width: 10%;
  outline: none;
  box-shadow: 0 0 0 0.2rem rgba(0, 123, 255, 0)!important;
  border: none;
}
.btn__icon {
  color: rgba(218, 218, 218, 0.6);
  height: 2.4rem;
  width: 2.4rem;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: auto;
  border-radius: 2px;
  font-size: 1.5em;
  cursor: pointer;
}
.btn__icon:hover {
  background-color: #eee;
}
.success:hover {
  color: #28a745;
}
.edit:hover {
  color: #343a40;
}
.delete:hover {
  color: #dc3545;
}

/********************************************************************** MEDIA */
@media (max-width: 1000px) {
  .tasks__wrapper {
    padding: 30% 10% 0 0;
    margin: 0;
  }
  .btn__icon {
    width: 1.5rem;
    height: 1.5rem;
    font-size: 1.3em;
  }
  .task__text {
    font-size: 0.9em;
  }
  .task__number {
    font-size: 4em;
  }
}
@media (max-width: 780px) {
  .tasks__wrapper {
    width: 70%;
    padding: 0;
    margin: 10% auto;
  }
  .task__number {
    font-size: 3.5em;
  }
  .btn__icon {
    width: 1.2rem;
    height: 1.2rem;
    font-size: 1em;
  }
}
@media (max-width: 450px) {
  .task__number {
    font-size: 2.7em;
  }
  .task__text {
    font-size: 0.7em;
  }
  .tasks__header {
    font-size: 1.2em;
  }
}

/******************************************************  CSS for "UserPage.js"*/

.user {
  width: 100vw;
  display: grid;
  grid-template-columns: 2fr 3fr;
  grid-template-rows: 4.5rem 1fr;
  grid-template-areas: "header header"
                       "info tasks";
}

/****************************************************************  User side  */
.user__info {
  width: 100%;
  padding: 0 5%;
  margin: auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  grid-area: info;
}

/***************************************************************** User image */
.user-img__wrapper {
  padding: 0.5rem;
  border-radius: 100%;
  border: 1px solid #eee;
}
.user-img__crop {
  position: relative;
  overflow: hidden;
  width: 15rem;
  height: 15rem;
  border-radius: 100%;
}
.user-img {
  position: absolute;
  width: 100%;
}

/********************************************************************** MEDIA */
@media (max-width: 1000px) {
  .user-img__crop {
    width: 12rem;
    height: 12rem;
  }
}
@media (max-width: 780px) {
    .user {
      grid-template-columns: 1fr;
      grid-template-rows: 4.5rem 1fr 3fr;
      grid-template-areas:
        "header header"
        "info info"
        "tasks tasks";
    }
    .user-img__wrapper {
      margin-top: 1rem;
    }
}
@media (max-width: 400px) {
  .user-img__crop {
    width: 8rem;
    height: 8rem;
  }
  .user {
    font-size: 0.8em;
  }
}

/****************** Media-queries are at the footer of every .css *************/

.Main {
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: center;
}
body {
  margin: 0;
  padding: 0;
}
* {
  color: #eee;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  background-color: #011c35;
  font-family: 'Comfortaa', cursive;
  font-weight: 300;
}

